import GlobalSettings from '@/GlobalSettings'
import { IKDataEntity } from 'metaflow-js'
import hillo from 'hillo'
import Category from './Category'
import CategoryType from './CategoryType'
import dayjs from 'dayjs'
// import Date from '.'

const entity = {
  dishId: {
    type: IKDataEntity.Types.Integer,
    displayName: 'id',
    tableConfig: {
      align: 'start',
    },
  },
  code: {
    displayName: 'code',
  },
  name: {
    displayName: 'name',
  },
  categoryId: {
    displayName: 'labelName',
    type: IKDataEntity.Types.Option,
    formConfig: {
      type: {
        selectItems: Category.getList,
        itemText: '_langsname',
      },
    },
  },
  categoryTypeId: {
    displayName: 'labelType',
    type: IKDataEntity.Types.Option,
    formConfig: {
      type: {
        selectItems: CategoryType.getList,
      },
    },
  },
  price: {
    displayName: 'price',
  },
  totalCount: {
    displayName: 'totalMoneyCount',
  },
  totalDelivery: {
    displayName: 'totalDelivery',
  },
}

const config = {
  load: async function (filter) {
    console.log(filter)
    const dishDCacheFilter = Object.assign({}, {
      from: filter[0].start ?? dayjs().format('YYYY-MM-DD'),
      to: filter[0].end ?? dayjs().format('YYYY-MM-DD'),
    })
    console.log('dishDeliveryFilter', dishDCacheFilter)

    const tempDCache = (await hillo.get('DCache.php', Object.assign({
      op: 'showAllDCache',
      lang: GlobalSettings.getLang(),
    }, dishDCacheFilter)))
      .content.filter(item => item.amount < 0)
      .reduce(function (pv, cv) {
        if (pv[cv.id]) {
          pv[cv.id] += cv.amount
        } else {
          pv[cv.id] = cv.amount
        }
        return pv
      }, {})

    // tempDCache.filter(item => item.amount > 0).reduce()

    console.log('tempDCache', tempDCache)
    const dishStatisticFilter = Object.assign({}, {
      start: filter[0].start ?? dayjs().format('YYYY-MM-DD'),
      end: filter[0].end ?? dayjs().format('YYYY-MM-DD'),
    })

    const dishStatistic = (await hillo.get('BackendData.php', Object.assign({
      op: 'dishStatistic',
      lang: GlobalSettings.getLang(),
    }, dishStatisticFilter)))
      .content.map(item => {
        const temp = item
        const index = temp.dishId
        // if (tempDCache[index] !== undefined) {
        if (tempDCache[index]) {
          temp.totalDelivery = tempDCache[index]
          console.log('temp, tempDCache[temp.dishId] ', temp, tempDCache[temp.dishId])
        } else temp.totalDelivery = 0

        return temp
      })

    console.log('dishStatistic', dishStatistic)

    return dishStatistic
  },
}

export default IKDataEntity.ModelFactory(entity, config)
